*:active, *:hover {
  outline: none;
}

body {
  background: rgba(242, 243, 246, 1);
}

#index-page {
  .header {
    position: absolute;
  }
}

#login-page, #register-page,
#password-request-page,
#password-reset-page {
  footer {
    display: none;
  }
}

.alert.info-alert {
  display: flex;
  align-items: center;
  position: fixed;
  right: 3em;
  top: 6em;
  z-index: 1000;

  button {
    margin-left: .5em;
  }

  @include media-breakpoint-down(md) {
    width: 90%;
    top: 1em;
    left: 5%;
  }
}

.form-outline-wrapper.invalid {
  .form-control {
    color: #2D3134;

    &:focus, &.active {
      &~ .form-notch {
        .form-notch-leading, .form-notch-middle, .form-notch-trailing {
          border-color: #f93154 !important;
        }
      }

      &~ .form-label {
        color: #f93154;
      }
    }

    &~ .form-notch {
      .form-notch-leading, .form-notch-middle, .form-notch-trailing {
        border-color: #f93154 !important;
      }
    }

    &~ .form-label {
      color: #f93154;
    }
  }

  .invalid-feedback {
    display: block;
  }
}

